import React from "react";
import Pdf from "./Pdf";
import { Box } from "@mui/system";
import { Divider } from "@mui/material";

export default class Pdfs extends React.Component {
    render() {
      const pdfs = this.props.pdfs;
      if (pdfs.length === 0) {
        return (
          <p></p>
        );
      }
      else {
        return(
            pdfs.map(
              (pdf, index) => 
              pdf === null ? null : 
              <Box sx={{mb: 3}}>
              <Pdf 
              key={pdf.id}
              id={pdf.id}
              filename={pdf.filename}
              username={pdf.username}
              date={pdf.date}
              body={pdf.body}
              readPdfs={this.props.readPdfs}
              loginUser = {this.props.loginUser}
              index={index}
              deletePdfs={this.props.deletePdfs}
              openErrorDialog={this.props.openErrorDialog}
            />
            </Box>
            )
      );
      }
    }
}

export {Pdfs};