import React from "react";
import axios from 'axios';

import {useForm} from 'react-hook-form';

import FullWidthTextField from "./TextField";
import LoadingButton from '@mui/lab/LoadingButton';
import Box from '@mui/material/Box';
import SendIcon from '@mui/icons-material/Send';
import SelectInput from "@mui/material/Select/SelectInput";


function getNow() {
  let now = new Date();
  let year = now.getFullYear();
	let mon = now.getMonth()+1; //
	let day = now.getDate();
	let hour = now.getHours();
	let min = now.getMinutes();
	let sec = now.getSeconds();
	let s = year + "" + mon + "" + day + "" + hour + "" + min + "" + sec + ""; 
	return s;
}

export default class Form extends React.Component {
    constructor(props) {
      super(props);
      this.state={
        value: '',
        sending: false
      }
      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    handleChange(event) {
      this.setState({value: event.target.value});
    }

    handleSubmit(event) {
      this.setState({sending: true});
      console.log(this.props.loginUser + "posting");
      const inputValue = this.state.value;
      const date = getNow();
      if(inputValue.length > 0) {
        axios.post('https://hurikakegohan.herokuapp.com/chats',
          {
            username: this.props.loginUser.nickname,
            body: inputValue,
            date: date
          },
          {headers:{
            uid: this.props.loginUser.uid,
            "access-token": this.props.loginUser.accessToken,
            client: this.props.loginUser.client
          }
          ,timeout: 5000
        })
        /* .then(() => {
          this.props.addPost()
          .then(() => {
            this.setState({sending: false});
            this.setState({value: ''})
          })
        }) */
        .then(() => {
          this.setState({sending: false});
          this.setState({value: ''});
        })
        .catch(() => {
          this.props.openErrorDialog();
          this.setState({sending: false});
        })
      }
      else {
        this.setState({sending: false});
      }
      event.preventDefault();
    }
  
    render() {
      return (
        <form onSubmit={this.handleSubmit}>
          <Box
            sx={{
              width: '100%'
            }}
          >
            <FullWidthTextField value={this.state.value} onChange={this.handleChange}/>
            <LoadingButton
              variant="contained" 
              type="submit"
              endIcon={<SendIcon />} 
              loading={this.state.sending}
              laodingPosition="end"
              sx={{mt: 0.5, marginLeft: '85%'}}
            >
              
            </LoadingButton>
          </Box>
        </form>
      );
    }
}

export {Form};