import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import reportWebVitals from './reportWebVitals';
import { render } from '@testing-library/react';

import axios from 'axios';

import BasicTabs from './MenuBar';
import SignIn from './LoginForm';
import ButtonAppBar from './Menu';
import { TenMp, Token } from '@mui/icons-material';
import ErrorDialog from './ErrorDialog';
import ActionCable from 'actioncable';

const root = ReactDOM.createRoot(document.getElementById('root'));

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loginError: false,
      loginState: false,
      loginUser:{
        uid: '',
        nickname: '',
        accessToken: '',
        client: ''
      },
      errorDialog: false
    };
    this.LogIn = this.LogIn.bind(this);
    this.LogOut = this.LogOut.bind(this);
    this.closeErrorDialog = this.closeErrorDialog.bind(this);
    this.openErrorDialog = this.openErrorDialog.bind(this);
  }

  closeErrorDialog() {
    this.setState({errorDialog: false});
  }

  openErrorDialog() {
    this.setState({errorDialog: true});
  }

  LogIn(inputEmail, inputPassword) {
    return new Promise((resolve, reject) => {
      axios.post('https://hurikakegohan.herokuapp.com/auth/sign_in',{
      email: inputEmail,
      password: inputPassword
      },{timeout:5000})
      .then((res) => {
        const resData = res.data.data;
        const resHeaders = res.headers;
        this.setState({
          loginError: false,
          loginState: true,
          loginUser:{
            nickname: resData.nickname,
            uid: resData.uid,
            accessToken: resHeaders['access-token'],
            client: resHeaders.client
          },
        });
        resolve(res);
      })
      .catch((error) => {
        this.setState({loginError: true});
        reject(error);
      })
    });
  }

  LogOut() {
    return new Promise((resolve, reject) => {
      axios.delete('https://hurikakegohan.herokuapp.com/auth/sign_out', {
       data:{uid: this.state.loginUser.uid},
       headers:
          {
            uid: this.state.loginUser.uid,
            "access-token": this.state.loginUser.accessToken,
            client: this.state.loginUser.client
          },
        timeout: 5000
      })
      .then((res) => {
        if(res.status === 200) {
          this.setState({
            loginState: false,
            loginUser:{
              nickname: '',
              uid: '',
              accessToken: '',
              client: '',
            }
          });
        }
      })
      .then((res) => resolve(res))
      .catch((res) => {reject(res)})
    });
  }

  render() {
    let element = <SignIn
      loginError={this.state.loginError}
      LogIn={this.LogIn}
      openErrorDialog={this.openErrorDialog}
    />
    if(this.state.loginState) {
      element = <BasicTabs loginUser={this.state.loginUser} openErrorDialog={this.openErrorDialog}/>
    }
    return (
      <div>
        <ButtonAppBar loginState={this.state.loginState} LogOut={this.LogOut} openErrorDialog={this.openErrorDialog}/>
        {element}
        {this.state.errorDialog && <ErrorDialog closeErrorDialog={this.closeErrorDialog}/>}
      </div>
    );
  }
}

root.render(<App />)

