import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import ListItemText from '@mui/material/ListItemText';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Slide from '@mui/material/Slide';
import { TextField } from '@mui/material';
import axios from 'axios';
import LoadingButton from '@mui/lab/LoadingButton';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

let date = null;
function getNow() {
    let now = new Date();
    let year = now.getFullYear();
      let mon = now.getMonth()+1; //
      let day = now.getDate();
      let hour = now.getHours();
      let min = now.getMinutes();
      let sec = now.getSeconds();
      let s = year + "" + mon + "" + day + "" + hour + "" + min + "" + sec + ""; 
    return date = s;
}

export default class PdfForm extends React.Component {
  constructor(props) {
    super(props);
    this.fileInput = React.createRef();
    this.state = {
      inputValue:{
        title: '',
        body: '',
      },
      inputError:{
        title:false,
        body: false,
        file: false
      },
      sending: false
    }
    this.handleClickOpen = this.handleClickOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.checkInputError = this.checkInputError.bind(this);
    this.checkInputFileError = this.checkInputFileError.bind(this);
  }

  handleClickOpen (event) {
    this.setState({open: true});
    this.setState({
        inputValue:{
            title: '',
            body: ''
        },
        inputError:{
          title:false,
          body: false,
          file: null
        },
        closeDisable: false
    })
  };

  handleClose (event) {
    this.setState({open: false});
  };

  handleSubmit (event) {
    this.setState({sending: true});
    this.setState({closeDisable: true});
    const date = getNow();
    const data = new FormData();
    data.append("username", this.props.loginUser.nickname);
    data.append("date", date);
    data.append("body",this.state.inputValue.body)
    data.append("filename", this.state.inputValue.title);
    data.append("pdf", this.fileInput.current.files[0]);
    if(this.checkInputError() && this.checkInputFileError()){
      axios.post('https://hurikakegohan.herokuapp.com/pdfs',
        data,
        {headers:{
          Accept: 'multipart/form-data',
          uid: this.props.loginUser.uid,
          "access-token": this.props.loginUser.accessToken,
          client: this.props.loginUser.client
        },
        timeout: 5000
      })
      .then((res) => {
        this.props.addPdf()
        .then(() => {
          this.setState({sending: false});
          this.setState({open: false});
          this.setState({closeDisable: false});
        })
      })
      .catch(() => {
        this.setState({sending: false});
        this.setState({closeDisable: false});
        this.props.openErrorDialog();
      })
    }
    else {
      this.setState({sending: false});
      this.setState({closeDisable: false});
    }
  }

  handleChange (event) {
    const target = event.target;
    const name= [target.name];
    const targetValue = event.target.value;
    this.setState({
      inputValue: {...this.state.inputValue,[name]: targetValue}
    })
  }

  checkInputError () {
    let res = true;
    let errors = {};
    for(const name in this.state.inputError) {
      if(name === 'file')continue;
      errors[name] = this.state.inputValue[name].length < 1;
      if(this.state.inputValue[name].length < 1) {
        res = false;
      }
    }
    this.setState({inputError:errors});
    return res;
  }

  checkInputFileError () {
      let res = true;
      if (this.fileInput.current.files[0] == null) {
          res = false;
          this.setState({
            inputError: {...this.state.inputError, file: true}
          });
      }
      return res;
  }

  render() {
    const errorMessage = ""
    return (
        <div>
          <Button variant="contained" onClick={this.handleClickOpen}>
            
          </Button>
          <Dialog
            fullScreen
            open={this.state.open}
            onClose={this.handleClose}
            TransitionComponent={Transition}
          >
            <AppBar sx={{ position: 'relative' }}>
              <Toolbar>
                <IconButton
                  edge="start"
                  color="inherit"
                  onClick={this.handleClose}
                  aria-label="close"
                  disabled={this.state.closeDisable}
                >
                  <CloseIcon />
                </IconButton>
                <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
                  
                </Typography>
                <LoadingButton autoFocus color="inherit" onClick={this.handleSubmit} loading={this.state.sending}>
                  
                </LoadingButton>
              </Toolbar>
            </AppBar>
            <List>
              <ListItem >
                <TextField
                    name="title"
                    value={this.state.inputValue.title}
                    label=""
                    fullWidth
                    onChange={this.handleChange}
                    inputProps={{maxLength: 100}}
                    error={this.state.inputError.title}
                    helperText={this.state.inputError.title ? errorMessage:null}
                />
              </ListItem>
              <ListItem >
                <TextField
                    name="body"
                    value={this.state.inputValue.body}
                    label=""
                    fullWidth
                    multiline
                    rows={5}
                    onChange={this.handleChange}
                    inputProps={{maxLength: 1000}}
                    error={this.state.inputError.body}
                    helperText={this.state.inputError.body ? errorMessage:null}
                />
              </ListItem>
              <ListItem >
                <label>
                  Upload file:
                  <input type="file" ref={this.fileInput} />
                </label>
              </ListItem>
              <ListItem>
                {this.state.inputError.file ?
                  <Typography variant="body2" component="div" color="red">
                    PDF
                  </Typography>
                  : null
                }
              </ListItem>
            </List>
          </Dialog>
        </div>
      );
  }
}
