import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import axios from 'axios';

import Chat from './Chat';
import Abstract from './Abstract';
import { ThreeSixty } from '@material-ui/icons';
import Documents from './Documents';
import { CircularProgress } from '@mui/material';
import ActionCable from 'actioncable';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default class BasicTabs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0,
      abstract:{},
      posts: [],
      pdfs: [],
      postRecordNum: 0,
      pdfRecordNum: 0,
      hasMorePosts: true,
      hasMorePdfs: true,
      readingAbstract: true,
      chatChannel: null
    }
    this.readAbstract();
    this.readPosts();
    this.readPdfs();
    this.handleChange = this.handleChange.bind(this);
    this.readAbstract = this.readAbstract.bind(this);
    this.editAbstract = this.editAbstract.bind(this);
    this.readPosts = this.readPosts.bind(this);
    this.readPdfs = this.readPdfs.bind(this);
    this.deletePosts = this.deletePosts.bind(this);
    this.deletePdfs = this.deletePdfs.bind(this);
    this.addPost = this.addPost.bind(this);
    this.addPdf = this.addPdf.bind(this);
  }

  componentDidMount() {
    if (this.state.chatChannel == null) {
      const cable= ActionCable.createConsumer(`wss://hurikakegohan.herokuapp.com/websocket/
        ?token=${this.props.loginUser.accessToken}&uid=${this.props.loginUser.uid}&client=${this.props.loginUser.client}`);
      this.setState({chatChannel: cable.subscriptions.create("ChatChannel", {
        connected: () => {console.log("connected")},
        disconnected: () => {console.log("disconnected")},
        rejected: () => {console.log("rejected")},
        received: (data) => {
          const tmp = this.state.posts.slice();
          tmp.unshift(data.content);
          this.setState({posts: tmp});
          this.setState({postRecordNum: this.state.postRecordNum + 1});
          console.log("received");
        }
      })});
    }
  }

  componentWillUnmount() {
    if (this.state.chatChannel != null) {
      this.state.chatChannel.unsubscribe();
      this.setState({chatChannel: null});
    }
  }

  handleChange(event, newValue) {
    this.setState({value: newValue});
  };

  readAbstract() {
    return new Promise((resolve, reject) => {
      console.log("readAbstract");
      this.setState({readingAbstract: true});
      axios.get('https://hurikakegohan.herokuapp.com/abstracts',{
        headers:
          {
            uid: this.props.loginUser.uid,
            "access-token": this.props.loginUser.accessToken,
            client: this.props.loginUser.client
          },
        timeout: 5000
      })
      .then((res) => {
        const res_posts = res.data;
        this.setState({abstract: res_posts});
        this.setState({readingAbstract: false});
        resolve(res);
      })
      .catch((error) => {
        this.setState({readingAbstract: false});
        this.props.openErrorDialog();
        reject(error);
      })
    });
  }

  editAbstract(date, title, body, textbook, form) {
    return new Promise((resolve, reject)=>{
      console.log("post")
      const url = 'https://hurikakegohan.herokuapp.com/abstracts/' + this.state.abstract.id;
      axios.patch(
        url,
        {
          date: date,
          title: title,
          body: body,
          textbook: textbook,
          form: form
        },
        {headers:
          {
            uid: this.props.loginUser.uid,
            "access-token": this.props.loginUser.accessToken,
            client: this.props.loginUser.client
          },
          timeout: 5000
        }
      )
      .then(() => {
        this.readAbstract()
        .then((res) => {resolve(res)})
        .catch((res) => {reject(res)})
      })
      .catch((res) => {
        this.props.openErrorDialog();
        reject(res);
      })
    });
  }

  readPosts() {
    return new Promise ((resolve, reject) => {
      let res_data;
      console.log("readPosts");
      axios.get('https://hurikakegohan.herokuapp.com/chats',{
        params:{recordNum: this.state.postRecordNum},
        headers:
        {
          uid: this.props.loginUser.uid,
          "access-token": this.props.loginUser.accessToken,
          client: this.props.loginUser.client
        },
        timeout: 5000
      })
      .then((res) => {
        res_data = res.data
        if(res_data.length > 0){
          const tmp = this.state.posts;
          tmp.push(...res_data);
          this.setState({posts: tmp});
          this.setState({postRecordNum: this.state.posts.length});
          this.setState({hasMorePosts: res_data.length > 0});
          console.log(`hasMore is ${this.state.hasMorePosts}`);
          resolve(res);
        }
        else {
          this.setState({hasMorePosts: res_data.length > 0});
          console.log(`hasMore is ${this.state.hasMorePosts}`);
          resolve(res);
        }
      })
      .catch((error) => {
        this.setState({hasMorePosts: false});
        console.log(`hasMore is ${this.state.hasMorePosts}`);
        this.props.openErrorDialog();
        reject(error);
      })
    });
  }

    readPdfs() {
      return new Promise ((resolve, reject) => {
        let res_data;
        console.log("readPdfs");
        axios.get('https://hurikakegohan.herokuapp.com/pdfs',{
          params:{recordNum: this.state.pdfRecordNum},
          headers:
          {
            uid: this.props.loginUser.uid,
            "access-token": this.props.loginUser.accessToken,
            client: this.props.loginUser.client
          },
          timeout: 5000
        })
        .then((res) => {
          res_data = res.data
          if(res_data.length > 0){
            const tmp = this.state.pdfs;
            tmp.push(...res_data);
            this.setState({pdfs: tmp});
            this.setState({pdfRecordNum: this.state.pdfs.length});
            this.setState({hasMorePdfs: res_data.length > 0});
            console.log(`hasMore is ${this.state.hasMorePdfs}`);
            resolve(res);
          }
          else {
            this.setState({hasMorePdfs: res_data.length > 0});
            console.log(`hasMore is ${this.state.hasMorePdfs}`);
          }
        })
        .catch((error) => {
          this.setState({hasMorePdfs: false});
          console.log(`hasMore is ${this.state.hasMorePdfs}`);
          this.props.openErrorDialog();
          reject(error);
        })
      });
    }

    deletePosts(index) {
      const tmp = this.state.posts.slice();
      tmp[index] = null;
      this.setState({posts:tmp});
      this.setState({postRecordNum: this.state.postRecordNum - 1});
    }

    deletePdfs(index) {
      const tmp = this.state.pdfs.slice();
      tmp[index] = null;
      this.setState({pdfs:tmp});
      this.setState({pdfRecordNum: this.state.pdfRecordNum - 1});
    }

    addPost(addedPost) {
      /* return new Promise((resolve, reject) => {
        axios.get('http://localhost:3001/chats/add',{
          headers:
            {
              uid: this.props.loginUser.uid,
              "access-token": this.props.loginUser.accessToken,
              client: this.props.loginUser.client
            },
          timeout: 5000
        })
        .then((res) => {
          const tmp = this.state.posts.slice();
          tmp.unshift(res.data);
          this.setState({posts: tmp});
          this.setState({postRecordNum: this.state.postRecordNum + 1});
          resolve(res);
        })
        .catch((error) => {
          this.props.openErrorDialog();
          reject(error);
        })
      }); */
    }

    addPdf() {
      return new Promise((resolve, reject) => {
        axios.get('https://hurikakegohan.herokuapp.com/pdfs/add',{
          headers:
            {
              uid: this.props.loginUser.uid,
              "access-token": this.props.loginUser.accessToken,
              client: this.props.loginUser.client
            },
          timeout: 5000
        })
        .then((res) => {
          const tmp = this.state.pdfs.slice();
          tmp.unshift(res.data);
          this.setState({pdfs: tmp});
          this.setState({pdfRecordNum: this.state.pdfRecordNum + 1});
          resolve(res);
        })
        .catch((error) => {
          this.props.openErrorDialog();
          reject(error);
        })
      });
    }

  render() {
    return (
      <Box sx={{ width: '100%'}}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider'}}>
          <Tabs value={this.state.value} onChange={this.handleChange} aria-label="basic tabs example">
            <Tab label="" {...a11yProps(0)} />
            <Tab label="" {...a11yProps(1)} />
            <Tab label="" {...a11yProps(2)} />
          </Tabs>
        </Box>
        
        <TabPanel value={this.state.value} index={0}>
          {this.state.readingAbstract ?
            <CircularProgress />:
            <Abstract
              abstract={this.state.abstract}
              readAbstract={this.readAbstract}
              editAbstract={this.editAbstract}
              openErrorDialog={this.props.openErrorDialog}
            />
          }
        </TabPanel>

        <TabPanel value={this.state.value} index={1}>
          <Chat
            loginUser={this.props.loginUser}
            readPosts={this.readPosts}
            posts={this.state.posts}
            hasMore={this.state.hasMorePosts}
            deletePosts={this.deletePosts}
            addPost={this.addPost}
            openErrorDialog={this.props.openErrorDialog}
            />
        </TabPanel>

        <TabPanel value={this.state.value} index={2}>
          <Documents
            loginUser={this.props.loginUser}
            readPdfs={this.readPdfs}
            pdfs={this.state.pdfs}
            hasMore={this.state.hasMorePdfs}
            deletePdfs={this.deletePdfs}
            addPdf={this.addPdf}
            openErrorDialog={this.props.openErrorDialog}
          />
        </TabPanel>
    </Box>
    );
  }
}
