import axios from "axios";
import React from "react";
import DownloadIcon from '@mui/icons-material/Download';

import AlertDialog from "./DeleteButton";

import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";
import MultiLineBody from "./MultiLineBody";
import LoadingButton from "@mui/lab/LoadingButton";

export default class Pdf extends React.Component {
  constructor(props) {
    super(props);
    this.onClickDelete = this.onClickDelete.bind(this);
    this.onClickDownload = this.onClickDownload.bind(this);
    this.state = {
      loading: false
    }
  }

  onClickDownload() {
    this.setState({loading: true});
    const url = 'https://hurikakegohan.herokuapp.com/pdfs/' + String(this.props.id);
    axios.get(url,{
      headers: {
          uid: this.props.loginUser.uid,
          "access-token": this.props.loginUser.accessToken,
          client: this.props.loginUser.client,
          'Content-type': 'application/pdf'
        },
      responseType :'arraybuffer',
      timeout: 5000
    })
    .then((res) => {
        /* let blob = new Blob([res.data],{type: "application/pdf"});
        console.log(blob);
        let a = document.createElement('a');
        let blobUrl = window.URL.createObjectURL(blob);
        a.style = 'display: none';
        a.href = blobUrl;
        a.download = this.props.filename + ".pdf";
        a.click(); */

        let blob = new Blob([res.data],{type: "application/pdf"});
        console.log(blob);
        let a = document.createElement('a');
        let blobUrl = window.URL.createObjectURL(blob);
        /* a.style = 'display: none';
        a.href = blobUrl;
        a.download = this.props.filename;
        a.click(); */
        window.open(blobUrl, '_blank');
        this.setState({loading: false});
    })
    .catch(() => {
      this.setState({loading: false});
      this.props.openErrorDialog();
    });
  }

  onClickDelete() {
    return new Promise((resolve, reject) => {
      const url = 'https://hurikakegohan.herokuapp.com/pdfs/' + String(this.props.id);
      axios.delete(url,{
        headers: {
          uid: this.props.loginUser.uid,
          "access-token": this.props.loginUser.accessToken,
          client: this.props.loginUser.client
        },
        timeout: 5000
      })
      .then((res) => {
        this.props.deletePdfs(this.props.index);
        resolve(res);
      })
      .catch((error) => {
        this.props.openErrorDialog();
        reject(error);
      })
    });
  }

  render() {
    return (
        <Card sx={{ minWidth: 275}} >
          <CardContent>
            <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              {this.props.date}<br />
              {this.props.username}
            </Typography>
            <Typography variant="h5" component="div">
              {this.props.filename}
            </Typography>
            <Typography variant="body1">
              {this.props.body == null ? 
                null : MultiLineBody(this.props.body)}
            </Typography>
          </CardContent>
          <CardActions>
            <Stack direction="row" spacing={2}>
              <LoadingButton loading={this.state.loading} size="small" variant="outlined" onClick={this.onClickDownload}>PDF</LoadingButton>
              {/* <Button size="small" variant="outlined" onClick={this.onClickDownload}></Button> */}
              {this.props.loginUser.nickname === this.props.username ? 
                <AlertDialog handleClickDelete={this.onClickDelete}/> :
                null
              }
            </Stack>
          </CardActions>
        </Card>
      );
  }
}

export {Pdf};