import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import LoadingButton from '@mui/lab/LoadingButton';

import axios from 'axios';
import { useState } from 'react';
import ErrorChips from './ErrorChips';
import { useEffect } from 'react';
import componentDidUpdate from 'react';
import { usePreviousProps } from '@mui/material/node_modules/@mui/utils';

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright  '}
      <Link color="inherit" href="http:localhost:3000">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const theme = createTheme();
let errorMessage = "";

export default function SignIn(props) {
  const [inputValue, setInput] = useState({email: "", password: ""});
  const [inputError, setInputError] = useState({email: false, password: false});
  const [sending, setSending] = useState(false);

  useEffect(()=>{console.log(sending)},[sending]);
  
  const handleSubmit = (event) => {
    event.preventDefault();
    setSending(true);
    checkInputError(inputValue.email, inputValue.password);
    if(inputValue.email.length > 0 && inputValue.password.length > 0) {
      props.LogIn(inputValue.email, inputValue.password)
      .then(() => {
        setSending(false);
      })
      .catch((error) => {
        setSending(false);
        errorMessage = "";
        if(axios.isAxiosError(error)){
          if(error.response.status === 401){
            errorMessage = "";
          }
          else {
            props.openErrorDialog();
          }
        }
        else {
          props.openErrorDialog();
        }
      })
    }
    else{
      console.log("else");
      setSending(false);
    }
  };

  const handleChange = (event) => {
    const target = event.target;
    const name = target.name;
    setInput({ ...inputValue, [name]: event.target.value} );
    setInputError({...inputError, [name]: inputValue[name].length < 0});
  }

  const checkInputError = (emailValue, passwordValue) => {
    setInputError({email: emailValue.length < 1, password: passwordValue.length < 1});
  }

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            
          </Typography>
          <Box name="tmp" component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              autoFocus
              inputProps={{maxLength: 30}}
              value={inputValue.email}
              onChange={handleChange}
              error={inputError.email}
              helperText={inputError.email ? "":null}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              value={inputValue.password}
              onChange={handleChange}
              inputProps={{maxLength: 30}}
              error={inputError.password}
              helperText={inputError.password ? "":null}
            />
            {/* <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            /> */}
            {props.loginError && <ErrorChips errorMessage={errorMessage}/>}
            <LoadingButton
              loading={sending}
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              
            </LoadingButton>
            {/* <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                  
                </Link>
              </Grid>
              <Grid item>
                <Link href="#" variant="body2">
                  {""}
                </Link>
              </Grid>
            </Grid> */}
          </Box>
        </Box>
        {/* <Copyright sx={{ mt: 8, mb: 4 }} /> */}
      </Container>
    </ThemeProvider>
  );
}