import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import { AppBar, CircularProgress } from '@mui/material';

export default function AlertDialog(props) {
  const [open, setOpen] = React.useState(false);
  const [deleting, setDeleting] = React.useState(false);

  React.useEffect(()=>{console.log(deleting)},[deleting]);

  const handleClickOpen = (props) => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleClickDelete = () => {
    setDeleting(true);
    props.handleClickDelete()
    .then(() => {setDeleting(false);})
    .catch(() => {
      setDeleting(false);
      setOpen(false);
      props.openErrorDialog();
    })
  }

  return (
    <div>
      <Button variant="outlined" color="error" size="small" onClick={handleClickOpen}>
        
      </Button>
      {deleting ? 
      (<Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {""}
        </DialogTitle>
        <DialogContent>
          <CircularProgress />
        </DialogContent>
      </Dialog>) :
      (<Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {""}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} autoFocus></Button>
          <Button onClick={handleClickDelete}>
            
          </Button>
        </DialogActions>
      </Dialog>)}
    </div>
  );
}
