import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import ListItemText from '@mui/material/ListItemText';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Slide from '@mui/material/Slide';
import { TextField } from '@mui/material';
import LoadingButton from '@mui/lab/LoadingButton';
import { TurnedIn } from '@material-ui/icons';
import { resolveObjectURL } from 'buffer';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

let date = null;
function getNow() {
    let now = new Date();
    let year = now.getFullYear();
      let mon = now.getMonth()+1; //
      let day = now.getDate();
      let hour = now.getHours();
      let min = now.getMinutes();
      let sec = now.getSeconds();
      let s = year + "" + mon + "" + day + "" + hour + "" + min + "" + sec + ""; 
      date = s;
}

export default class FullScreenDialog extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      inputValue:{
        title: this.props.abstract.title,
        body: this.props.abstract.body,
        textbook: this.props.abstract.textbook,
        form: this.props.abstract.form
      },
      inputError:{
        title:false,
        body: false,
        textbook: false,
        form: false
      },
      sending: false,
      closeDisable: false
    }
    this.handleClickOpen = this.handleClickOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.checkInputError = this.checkInputError.bind(this);
  }

  handleClickOpen (event) {
    this.setState({open: true});
    this.setState({
      inputValue:{
        title: this.props.abstract.title,
        body: this.props.abstract.body,
        textbook: this.props.abstract.textbook,
        form: this.props.abstract.form
      },
      inputError:{
        title:false,
        body: false,
        textbook: false,
        form: false
      }
    })
  };

  handleClose (event) {
    this.setState({open: false});
  };

  handleSubmit (event) {
    this.setState({sending: true});
    this.setState({closeDisable: true});
    if(this.checkInputError()){
      getNow();
      /* this.props.editAbstract(
        date,
        this.state.inputValue.title,
        this.state.inputValue.body,
        this.state.inputValue.textbook,
        this.state.inputValue.form
      ) */
      this.props.editAbstract(
        date,
        this.state.inputValue.title,
        this.state.inputValue.body,
        this.state.inputValue.textbook,
        this.state.inputValue.form
      )
      .then((res) => {
        this.setState({sending: false});
        this.setState({open: false});
        this.setState({closeDisable: false})
      })
      .catch((res) => {
        this.setState({sending: false});
        this.setState({closeDisable: false})
        this.props.openErrorDialog();
      })
    }
    else {
      this.setState({sending: false});
      this.setState({closeDisable: false})
    }
  }

  handleChange (event) {
    const target = event.target;
    const name= [target.name];
    const targetValue = event.target.value;
    this.setState({
      inputValue: {...this.state.inputValue,[name]: targetValue}
    })
  }

  checkInputError () {
    let res = true;
    let errors = {};
    for(const name in this.state.inputError) {
      errors[name] = this.state.inputValue[name].length < 1;
      if(this.state.inputValue[name].length < 1) {
        res = false;
      }
    }
    this.setState({inputError:errors});
    return res;
  }

  render() {
    const errorMessage = ""
    return (
        <div>
          <Button variant="contained" onClick={this.handleClickOpen}>
            
          </Button>
          <Dialog
            fullScreen
            open={this.state.open}
            onClose={this.handleClose}
            TransitionComponent={Transition}
          >
            <AppBar sx={{ position: 'relative' }}>
              <Toolbar>
                <IconButton
                  edge="start"
                  color="inherit"
                  onClick={this.handleClose}
                  aria-label="close"
                  disabled={this.state.closeDisable}
                >
                  <CloseIcon />
                </IconButton>
                <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
                  
                </Typography>
                <LoadingButton loading={this.state.sending} autoFocus color="inherit" onClick={this.handleSubmit}>
                  
                </LoadingButton>
              </Toolbar>
            </AppBar>
            <List>
              <ListItem >
                <TextField
                    name="title"
                    value={this.state.inputValue.title}
                    label=""
                    fullWidth
                    onChange={this.handleChange}
                    inputProps={{maxLength: 100}}
                    error={this.state.inputError.title}
                    helperText={this.state.inputError.title ? errorMessage:null}
                />
              </ListItem>
              <ListItem >
                <TextField
                    name="body"
                    value={this.state.inputValue.body}
                    label=""
                    fullWidth
                    multiline
                    rows={5}
                    onChange={this.handleChange}
                    inputProps={{maxLength: 1000}}
                    error={this.state.inputError.body}
                    helperText={this.state.inputError.body ? errorMessage:null}
                />
              </ListItem>
              <ListItem >
                <TextField
                    name="textbook"
                    value={this.state.inputValue.textbook}
                    label=""
                    fullWidth
                    multiline
                    rows={5}
                    onChange={this.handleChange}
                    inputProps={{maxLength: 1000}}
                    error={this.state.inputError.textbook}
                    helperText={this.state.inputError.textbook ? errorMessage:null}
                />
              </ListItem>
              <ListItem >
                <TextField
                    name="form"
                    value={this.state.inputValue.form}
                    label=""
                    fullWidth
                    multiline
                    rows={5}
                    onChange={this.handleChange}
                    inputProps={{maxLength: 1000}}
                    error={this.state.inputError.form}
                    helperText={this.state.inputError.form ? errorMessage:null}
                />
              </ListItem>
            </List>
          </Dialog>
        </div>
      );
  }
}
