import axios from "axios";
import React from "react";
import AlertDialog from "./DeleteButton";
import { Button } from "@mui/material";
import BasicCard from "./ErrorChips";
import { Paper } from "@mui/material";
import { Box, flexbox } from "@mui/system";
import { Typography } from "@mui/material";
import {MultiLineBody} from "./MultiLineBody";


export default class Post extends React.Component {
  constructor(props) {
    super(props);
    this.onClickDelete = this.onClickDelete.bind(this);
  }

  onClickDelete() {
    return new Promise((resolve, reject) => {
      console.log("delete stated");
      const url = 'https://hurikakegohan.herokuapp.com/chats/' + String(this.props.id);
      axios.delete(url,{
        headers: {
          uid: this.props.loginUser.uid,
          "access-token": this.props.loginUser.accessToken,
          client: this.props.loginUser.client
        },
        timeout: 5000
      })
      .then((res) => {
        this.props.deletePosts(this.props.index);
        resolve(res);
      })
      .catch((error)=> {
        reject(error);
      })
    });
  }

  render() {
    const myChat= (
      <Box sx={{ width: "40%", mb: 4, marginLeft: '55%' }}>
        <Typography sx={{  }} variant="body2" color="text.secondary" gutterBottom>
          {this.props.date}
        </Typography>
        <Paper sx={{ mb: 0.5, p: 2, backgroundColor: "#99DDFF", color: "black" }} variant="outlined" >
          <Typography sx={{  }} variant="body1" gutterBottom>
          {this.props.body == null ? 
            null : MultiLineBody(this.props.body)}
          </Typography>
        </Paper>
        <AlertDialog handleClickDelete={this.onClickDelete} openErrorDialog={this.props.openErrorDialog}/>
      </Box>
    );
    const otherChat = (
      <Box sx={{ width: "40%", mb: 4, marginLeft: '5%' }} >
        <Typography sx={{  }} variant="body2" color="text.secondary" gutterBottom>
          {this.props.date}
        </Typography>
        <Paper sx={{ p: 2, backgroundColor: "#CCCCCC", color: "black" }} variant="outlined">
          <Typography sx={{  }} variant="body1" gutterBottom>
            {this.props.body == null ?
              null : MultiLineBody(this.props.body)}
          </Typography>
        </Paper>
        <Typography sx={{ }} variant="body2" color="text.secondary" gutterBottom>
         {this.props.username}
        </Typography>
      </Box>
    );
    const whosChat = this.props.username === this.props.loginUser.nickname;
    return (
      whosChat ? myChat:otherChat
    );
  }
}

export {Post};